@import "./../../variables.scss";

.swiper {
  width: 100%;
  height: calc(100% - 50px);
  min-height: auto;
  background-color: darkgrey;
  color: #fff;
  border-radius: 10px 10px 0 0;

  .swiper-slide {
    width: 100% !important;
    height: 100%;
  }

  .swip-slide {
    padding: 24px 30px;
    font-family: "Montserrat";
    height: 100%;
    width: 100%;
    background-size: cover;

    h1 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    h3 {
      font-size: 20px;
      margin-bottom: 10px;
    }

    p {
      font-size: 14px;
    }
  }
}

.slider {
  background-color: darkgray;
  width: 70%;
  border-radius: 15px;
  height: 100%;
}

.nav-btns {
  background-color: $secondary-color;
  position: relative;
  bottom: 0;
  z-index: 5;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  border-radius: 0 0 10px 10px;

  .nav-btn {
    border: 0;
    background-color: transparent;
    padding: 0;
    margin: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    position: relative;
    margin: 0 20px;
  }
}

.swiper-pagination-bullet {
  height: 10px;
  width: 10px;
  border-radius: 6px;
  background-color: #fff;
  opacity: unset;
  margin-top: 30px;
  cursor: pointer;
}

.swiper-custom-bullet-active {
  background-color: #505050;
  width: 17px;
  transition: width 200ms;
}

.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after,
.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
  content: "";
}

.custom-pagination {
  display: flex;
  justify-content: center;
  align-self: center;
}